import initState from "./initState";

let initRed = (state = initState, action) => {
  switch (action.type) {   
    case "LOGIN_USER" : return { ...state, curUserFIO : action.data.FIO, curUserId : 1, curPage : "MainPage"}
    case "CHANGE_STATE_BOOK_MODAL" : return { ...state, isShowBookModal : !state.isShowBookModal}
    case "CHANGE_STATE_NEWS_MODAL" : return { ...state, isShowNewsModal : !state.isShowNewsModal}
    case "CHANGE_STATE_TEST_MODAL" : return { ...state, isShowTestModal : !state.isShowTestModal}
    case "SHOW_CARD" : return { ...state, curSubPage : action.cardName}
    default:
      return state;
  }
};

export default initRed;
