import React from 'react';
import './Animations.scss';


class Animations extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            animArr : [
                {
                    "title" : "anim0",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim1",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim2",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim3",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim4",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim5",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "anim6",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
            ]
        }
    }
    render() {
        return (
            <div className = "Animations">                
                    { this.state.animArr.map( (el) => (
                        <div className = "Animations_Anim">
                            <p> {el.title} </p>
                        </div>
                    ))}                
            </div>
        )
    }
}

export default Animations;