import React from 'react';
import './BigMenu.scss';

import { connect } from 'react-redux';
import AShowCard  from '../../store/actions/AShowCard';

class BigMenu extends React.Component {
    constructor ( props ) {
        super ( props ) ;
        this.state = {
            menuItems : [
                { 
                    "title" : "Напрацоўкі",
                    "id"    : 0
                },
                { 
                    "title" : "Гульні",
                    "id"    : 1 
                },
                { 
                    "title" : "Анімацыі",
                    "id"    : 2 
                },
                { 
                    "title" : "Тэсты",
                    "id"    : 3
                },
                { 
                    "title" : "Творчасць",
                    "id"    : 4
                },
                { 
                    "title" : "Стратэгія",
                    "id"    : 5
                },
                { 
                    "title" : "Мысленне",
                    "id"    : 6                    
                }
            ],
            curShowItems : [
                
            ],
            curShowIndex : 0
        }
        this.slideLeft = this.slideLeft.bind( this );
        this.slideRight = this.slideRight.bind ( this );   
        this.NavClickHandler = this.NavClickHandler.bind( this );
    }
    componentDidMount() {
        this.setState((state) => ({
            ...state,
            curShowItems : [ state.menuItems[0],state.menuItems[1],state.menuItems[2],state.menuItems[3] ]  
        }))
    }
    slideLeft () {
        if ( this.state.curShowIndex > 0 ) {
            let ind = this.state.curShowIndex -1;
            this.setState((state) => ({
                ...state,
                curShowIndex : state.curShowIndex - 1,
                curShowItems : [ state.menuItems[ind],state.menuItems[ind+1],state.menuItems[ind+2],state.menuItems[ind+3] ]  
            }))
        }
    }
    slideRight () {
        if ( this.state.curShowIndex < this.state.menuItems.length - 4) {
            let ind = this.state.curShowIndex +1 ;
            this.setState((state) => ({
                ...state,
                curShowIndex : state.curShowIndex + 1,
                curShowItems : [ state.menuItems[ind],state.menuItems[ind+1],state.menuItems[ind+2],state.menuItems[ind+3] ]  
            }))
        }
    }
    NavClickHandler( xx ) {
        this.props.switchToPage(xx)
    }
    render() {
        return (
            <div className = "BigMenu">
                <input  
                    type = "submit"
                    value = "<"
                    onClick = { this.slideLeft }
                    className = "BM_slider__btn__left"></input>
                <input 
                    type = "submit"
                    value = ">"
                    onClick = { this.slideRight}
                    className = "BM_slider__btn__right"></input>
                { this.state.curShowItems.map ( el => (
                    <div 
                        className = "BM_item"                     
                        id = {"BM_item"+el.id}
                        onClick = { (xx) => this.NavClickHandler( el.title )}
                        key = { el.id }
                        >                       
                    </div>
                ))}
            </div>
        )
    }
}


let mapState = state => {
    return {
        curPage : state.curSubPage
    }
}

let mapDispatch = dispatch => {
    return {
        switchToPage : ( xx ) => dispatch( AShowCard(xx) )
    }
}



export default connect ( mapState, mapDispatch) (BigMenu);