import React from 'react';
import './Games.scss';


class Games extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            gamesArr : [
                {
                    "title" : "game0",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game1",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game2",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game3",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game4",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game5",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
                {
                    "title" : "game6",
                    "path"  : "path0",
                    "back"  : "backImg0"
                },
            ]
        }
    }
    render() {
        return (
            <div className = "Games">                
                    { this.state.gamesArr.map( (el) => (
                        <div className = "Games_Game">
                            <p> {el.title} </p>
                        </div>
                    ))}                
            </div>
        )
    }
}

export default Games;