import React from 'react';
import './MainPage.scss';

import { connect } from 'react-redux';
import AShowCard  from '../../store/actions/AShowCard';

import Manuals from '../Cards/Manuals/Manuals';
import BigMenu from '../BigMenu/BigMenu';
import Napracouki from '../Cards/Napracouki/Napracouki';
import Games from '../Cards/Games/Games';
import Animations from '../Cards/Animations/Animations';
import Tests from '../Cards/Tests/Tests';
import Tvorch from '../Cards/Tvorch/Tvorch';
import Strat from '../Cards/Strat/Strat';
import Mish from '../Cards/Mish/Mish';

class MainPage extends React.Component {
    constructor ( props ) {
        super ( props );
        
            this.state = {               
            curShowPage : <BigMenu />
        }
        this.NavClickHandler = this.NavClickHandler.bind( this );
    }
    static getDerivedStateFromProps(nextProps, state) {
        let curComp = <BigMenu />;
        switch ( nextProps.curPage ) {
          case "Manual"  : state.curShowPage = <Manuals />; break;
          case "Напрацоўкі"   : state.curShowPage = <Napracouki />; break;       
          case "Гульні"   : state.curShowPage = <Games />; break; 
          case "Анімацыі"   : state.curShowPage = <Animations />; break;  
          case "Тэсты"   : state.curShowPage = <Tests />; break;       
          case "Творчасць"   : state.curShowPage = <Tvorch />; break;  
          case "Стратэгія"   : state.curShowPage = <Strat />; break;
          case "Мысленне"   : state.curShowPage = <Mish />; break;
          case "MainPage" : state.curShowPage = <BigMenu />; break;
          default : curComp = <BigMenu />;
        }
        return {
          ...state,
          curPage : curComp
        };
      }
      NavClickHandler( xx ) {
          this.props.switchToPage(xx)
      }
    render() {
        return (
            <div className = "MainPage">
                <div className = "MainNav">
                    <ul className = "HeadNav">
                        <li onClick = { (xx) => this.NavClickHandler( "MainPage" ) } >Галоўнае меню</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Напрацоўкі" ) }>Напрацоўкі</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Гульні" ) }>Гульні</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Анімацыі" ) }>Анімацыі</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Тэсты" ) }>Тэсты</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Творчасць" ) }>Творчасць</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Стратэгія" ) }>Стратэгія</li>
                        <li onClick = { (xx) => this.NavClickHandler( "Мысленне" ) }>Мысленне</li>                       
                    </ul>
                </div>  
                <div className = "BigMenuPage">
                    { this.state.curShowPage }
                </div> 
                <div className = "FootNav">
                  {/* <p className = "FootNavHeader">Карысныя спасылкі</p> */}
                    <ul className = "FootNavList">
                        <li onClick = { (xx) => this.NavClickHandler( "Manual")}>Падручнікі і кнігі</li>
                        <li>Літаратурная палічка</li>  
                        <li>Літаратурная палічка</li>
                        <li>Літаратурная палічка</li> 
                        <li>Літаратурная палічка</li>                 
                        
                    </ul>
                </div>
            </div>
        )
    }
}

let mapState = state => {
    return {
        curPage : state.curSubPage
    }
}

let mapDispatch = dispatch => {
    return {
        switchToPage : ( xx ) => dispatch( AShowCard(xx) )
    }
}

export default connect(mapState, mapDispatch) (MainPage);